/* eslint-disable prefer-template */

import React, { useEffect, useState } from 'react';
import propTypes from 'prop-types';
import Main from '../layouts/Main';
import Post from '../components/Post';
import blogs from '../data/blog/blog';

const collectMetatada = (markdown) => {
  const count = markdown.split(/\s+/)
    .map((s) => s.replace(/\W/g, ''))
    .filter((s) => s.length).length;

  const metadataRegex = /\[_metadata_:(?<field>(\w)*)(]:-\s")(?<value>[\w\s\d/'+\-,]*)(")/g;
  const metadata = [...markdown.matchAll(metadataRegex)]
    .reduce((acc, match) => ({ ...acc, [match[1]]: match[4] }), {});

  return { ...metadata, wordcount: count, markdown: markdown.replace(metadataRegex, '') };
};

const parseDate = (dateString) => {
  const [day, month, year] = dateString.split('/').map((part) => parseInt(part, 10));
  return new Date(year, month - 1, day);
};

const sortByDate = (a, b) => {
  const dateA = parseDate(a.date);
  const dateB = parseDate(b.date);
  return dateA - dateB;
};

const Blog = ({ children }) => {
  const [blogContent, setBlogContent] = useState([]);

  useEffect(() => {
    blogs.map((blog) => {
      import('./../data/blog/' + blog.filename)
        .then(res => {
          fetch(res.default)
            .then(response => response.text())
            .then(response => {
              setBlogContent(currBlogContent => [
                ...currBlogContent,
                collectMetatada(response),
              ].sort(sortByDate));
            });
        });
      return true;
    });
  }, [children]);

  return (
    <Main
      title='Blog'
      description='Software Development, Cloud Architecture and 3D Printing Articles'
    >
      {blogContent.map(post => (<Post key={post.date} post={post} />))}
    </Main>
  );
};

Blog.propTypes = {
  children: propTypes.string,
};

export default Blog;
