import React from 'react';
import propTypes from 'prop-types';
import { Link } from 'react-router-dom';
import Markdown from 'markdown-to-jsx';
import Code from './Code';

const Post = ({ post }) => (
  <article className='post markdown' id={`post${post.date}`}>
    <header>
      <div className='title'>
        <h2 data-testid='heading'><Link to='/Blog'>{post.title}</Link></h2>
        <p>(in about {post.wordcount} words)</p>
        <p>posted on {post.date} By Ofir Yaron</p>
      </div>
    </header>
    <div className='container'>
      <div className='post-wrapper'>
        <Markdown options={{
          overrides: {
            code: {
              component: Code,
            },
          },
        }}
        >
          {post.markdown}
        </Markdown>
      </div>
    </div>
  </article>
);

Post.propTypes = {
  post: propTypes.object,
};

export default Post;
